<template>
	<view>
		<view class="shopcart">
			<view class="content" @tap="toggleList">
				<view class="content-left">
					<view class="logo-wrapper">
						<view class="logo" :class="{'highlight':totalCount>0}">
							<text class="icon icon-shopping_cart" :class="{'highlight':totalCount>0}"></text>
						</view>
						<view class="num" v-show="totalCount>0">
							{{totalCount}}
						</view>
					</view>
					<view class="price" :class="{'highlight':totalCount>0}">￥{{totalPrice}}</view>
					<view class="desc">另需配送费￥{{deliveryPrice}}元</view>
				</view>
				<view class="content-right" @tap.stop="pay">
					<view class="pay" :class="payClass">
						{{payDesc}}
					</view>
				</view>
			</view>
			<view class="ball-container">
				<view class="animation">
					<view class="ball">
						<!-- <view class="inner inner-hook"></view> -->
					</view>
				</view>
			</view>
			<view class="animation">
				<view class="shopcart-list" v-show="listShow" :animation="toggleAnimationData">
					<view class="list-header">
						<text class="title">购物车</text>
						<text class="empty" @tap="empty">清空</text>
					</view>
					<scroll-view class="list-content" id="listContent" scroll-y="true">
						<view class="food" v-for="(food,index) in selectFoods" :key="index">
							<text class="name">{{food.name}}</text>
							<view class="price">
								<text>￥{{food.price * food.count}}</text>
							</view>
							<view class="cartcontrol-wrapper">
								<cartcontrol :food="food"></cartcontrol>
							</view>
						</view>
					</scroll-view>
				</view>
			</view>

		</view>
		<view class="animation">
			<view class="list-mask" v-show="listShow" @tap="hideList"></view>
		</view>
	</view>
</template>

<script>
	import cartControl from '../cartcontrol/cartcontrol.vue'
	export default {
		props: {
			deliveryPrice: {
				type: Number,
				default: 0
			},
			minPrice: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				selectFoods: [{
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}, {
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}, {
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}, {
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}, {
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}, {
					price: 20,
					name: "皮蛋瘦肉粥",
					count: 1
				}],
				fold: true,
				toggleAnimationData: {}
			};
		},
		computed: {
			totalPrice() {
				let total = 0;
				this.selectFoods.forEach((food) => {
					total += food.price * food.count;
				});
				return total;
			},
			totalCount() {
				let count = 0;
				this.selectFoods.forEach((food) => {
					count += food.count;
				});
				return count;
			},
			payDesc() {
				if (this.totalPrice === 0) {
					return `￥${this.minPrice}元起送`;
				} else if (this.totalPrice < this.minPrice) {
					let diff = this.minPrice - this.totalPrice;
					return `还差￥${diff}元起送`;
				} else {
					return '去结算';
				}
			},
			payClass() {
				if (this.totalPrice < this.minPrice) {
					return 'not-enough';
				} else {
					return 'enough';
				}
			},
			listShow() {
				if (!this.totalCount) {
					this.fold = true;
					return false;
				}
				let show = !this.fold;
				// if (show) {
				//   this.$nextTick(() => {
				//     if (!this.scroll) {
				//       this.scroll = new BScroll(this.$refs.listContent, {
				//         click: true
				//       });
				//     } else {
				//       this.scroll.refresh();
				//     }
				//   });
				// }
				return show;
			}
		},
		methods: {
			toggleList() {
				if (!this.totalCount) {
					return
				}
				this.fold = !this.fold
			},
			// 结账方法
			pay() {
				if (this.totalPrice < this.minPrice) {
					return;
				}
				window.alert(`支付${this.totalPrice}元`);
			},
			empty() {
				this.selectFoods.forEach((food) => {
					food.count = 0;
				});
			},
			hideList() {
				this.fold = true;
			},
			leave() {
				let leave = uni.createAnimation()
				leave.translate3d(0, -247, 0).step({
					duration: 0
				}).step()
				leave.translate3d(0, 0, 0).step({
					duration: 500
				})
				this.toggleAnimationData = leave.export()
			},
			enter() {
				const HEIGHT = 48
				let cout = this.selectFoods.length
				let offset = HEIGHT * cout + 40 > 247 ? 247 : HEIGHT * cout + 40
				let toggle = uni.createAnimation()
				toggle.translate3d(0, 0, 0).step({
					duration: 0
				})
				toggle.translate3d(0, -offset, 0).step({
					duration: 500
				})

				this.toggleAnimationData = toggle.export()
			}
		},
		watch: {
			fold(old, cur) {
				if (cur) {
					this.enter()
				} else {
					this.leave()
				}
			}
		},
		components: {
			"cartcontrol": cartControl
		}
	}
</script>

<style>
	.shopcart {
		position: fixed;
		bottom: 90rpx;
		left: 0;
		z-index: 50;
		width: 100%;
		height: 96rpx;
		/* background-color: #007AFF; */
	}

	.shopcart .content {
		display: flex;
		background-color: #141d27;
		font-size: 0;
		color: rgba(255, 255, 255, .4);
	}

	.shopcart .content .content-left {
		flex: 1;
	}

	.shopcart .content .content-left .logo-wrapper {
		display: inline-block;
		vertical-align: top;
		position: relative;
		top: -20rpx;
		margin: 0 24rpx;
		padding: 12rpx;
		width: 112rpx;
		height: 112rpx;
		box-sizing: border-box;
		border-radius: 50%;
		background-color: #141d27;
	}

	.shopcart .content .content-left .logo-wrapper .logo {
		width: 100%;
		height: 100%;
		border-radius: 50%;
		text-align: center;
		background-color: #2b343c;
	}

	.shopcart .content .content-left .logo-wrapper .logo.highlight {
		background-color: rgb(0, 160, 220);
	}

	.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
		line-height: 88rpx;
		font-size: 48rpx;
		color: #80858a;
	}

	.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight {
		color: #FFFFFF;
	}

	.shopcart .content .content-left .logo-wrapper .num {
		position: absolute;
		top: 0;
		right: 0;
		width: 48rpx;
		height: 32rpx;
		line-height: 32rpx;
		text-align: center;
		border-radius: 32rpx;
		font-size: 18rpx;
		font-size: 700;
		color: #fff;
		background-color: rgb(240, 20, 20);
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
	}

	.shopcart .content .content-left .price {
		display: inline-block;
		vertical-align: top;
		margin-top: 24rpx;
		line-height: 48rpx;
		padding-right: 24rpx;
		box-sizing: border-box;
		border-right: 1px solid rgba(255, 255, 255, 0.1);
		font-size: 32rpx;
		font-weight: 700;
	}

	.shopcart .content .content-left .price.highlight {
		color: #FFFFFF;
	}

	.shopcart .content .content-left .desc {
		display: inline-block;
		vertical-align: top;
		margin: 24rpx 0 0 24rpx;
		line-height: 48rpx;
		font-size: 20rpx;
	}

	.shopcart .content .content-right {
		flex: 0 0 210rpx;
		width: 210rpx;
	}

	.shopcart .content .content-right .pay {
		height: 96rpx;
		line-height: 96rpx;
		text-align: center;
		font-size: 24rpx;
		font-weight: 700;
	}

	.shopcart .content .content-right .pay.not-enough {
		background-color: #2b333b;
	}

	.shopcart .content .content-right .pay.enough {
		background-color: #00b43c;
		color: #FFFFFF;
	}

	.shopcart .ball-container .ball {
		position: fixed;
		left: 64rpx;
		/* bottom: 44rpx; */
		bottom: 134rpx;
		z-index: 200;
		transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
	}

	.shopcart .ball-container .ball .inner {
		width: 32rpx;
		height: 32rpx;
		border-radius: 50%;
		background-color: rgb(0, 160, 220);
		transition: all .4s linear;
	}

	.shopcart .shopcart-list {
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
		width: 100%;
		/* transform: translate3d(0, -100%, 0); */
		transform: translate3d(0, 0, 0);
	}

	/* 动画 */
	.shopcart .shopcart-list .list-header {
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 36rpx;
		background-color: #f3f5f7;
		border-bottom: 2rpx solid rgba(7, 17, 27, 0.1);
	}

	.shopcart .shopcart-list .list-header .title {
		float: left;
		font-size: 28rpx;
		color: rgb(7, 17, 27);
	}

	.shopcart .shopcart-list .list-header .empty {
		float: right;
		font-size: 24rpx;
		color: rgb(0, 160, 220);
	}

	.shopcart .shopcart-list .list-content {
		padding: 0 36rpx;
		max-height: 434rpx;
		overflow: hidden;
		background-color: #FFFFFF;
	}

	.shopcart .shopcart-list .list-content .food {
		position: relative;
		padding: 24rpx 0;
		box-sizing: border-box;
	}

	.shopcart .shopcart-list .list-content .food::after {
		position: absolute;
		content: '';
		display: block;
		left: 0;
		bottom: 0;
		width: 100%;
		border-top: 2rpx solid rgba(7, 17, 27, .1);
	}

	.shopcart .shopcart-list .list-content .food .name {
		line-height: 48rpx;
		font-size: 28rpx;
		color: rgb(7, 17, 27);
	}

	.shopcart .shopcart-list .list-content .food .price {
		position: absolute;
		right: 180rpx;
		bottom: 24rpx;
		line-height: 48rpx;
		font-size: 28rpx;
		font-weight: 700;
		color: rgb(240, 20, 20);
	}

	.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
		position: absolute;
		right: 0;
		bottom: 12rpx;
	}

	.list-mask {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 40;
		backdrop-filter: blur(10px);
		opacity: 1;
		background-color: rgba(7, 17, 27, .6);
	}

	/* 动画 */
</style>
